.top-extra-header {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
    align-items: center;

    .left-header {
        h3 {
            font-family: 'Gilroy bold', sans-serif;
            font-size: 17px;
            font-weight: bold;
            color: #414D6A;
        }
    }

    .right-header {
        .account {
            font-family: 'Gilroy normal', sans-serif;
            font-size: 17px;
            color: #304FFF;
        }

        .adding {
            padding: 10px 22px 10px 47px;
            background: transparent;
            outline: none;
            border: none;
            font-size: 16px;
            color: #000000;
            border: 1px solid #41D481;
            border-radius: 100px 5px 5px 100px;
            position: relative;
            margin-left: 25px;

            &::before {
                content: '+';
                background: #41D481;
                padding: 7px 9px 9px 10px;
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                font-size: 25px;
                border-radius: 50%;
                margin: auto 0;
            }
        }
    }
}

.bottom-extra-header {
    .top-button-group {
        display: flex;

        .status-header-button {
            font-size: 17px;
            color: #414D6A;
            margin-right: 25px;
            border: none;
            outline: none;
            transition: all ease 0.5s;
            background: transparent;
            &.active{
                color: #304FFF;
            }
        }
    }
    .bottom-line{
        margin: 15px 0;
        position: relative;
        .line{
            width: 100%;
            height: 2px;
            background:  rgba(151, 151, 151, 0.600459);
        }
        .active-line{
            width: 100px;
            height: 3px;
            position: absolute;
            background: #304FFF;
            top: 0;
            bottom: 0;
            margin: auto 0;
            transition: all ease 0.5s;
            &.new{
                left: 0px;
            }
            &.message{
                left: 120px;
            }
            &.payment{
                left: 250px;
            }
            &.settings{
                left: 380px;
            }
            &.bussines{
                left: 500px;
            }
        }
    }
}