*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

@font-face {
    font-family: 'Gilroy normal';
    src: url(./fonts/Galien-Regular.woff);
}

@font-face {
    font-family: 'Gilroy bold';
    src: url(./fonts/Gilroy-ExtraBold.woff);
}

@mixin normalFont {
    font-family: 'Gilroy normal', sans-serif;
}

@mixin boldFont {
    font-family: 'Gilroy bold', sans-serif;
}

body{
    @include normalFont;
}

.container{
    max-width: 1144px;
    margin: 0 auto;
}