.header-extra {
    width: 100%;
    background: #FFFFFF;
    box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
    padding: 17px 0;

    a {
        text-decoration: none;
    }

    ul {
        list-style: none;
        display: flex;
    }

    .container {
        .header-top {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;

            .header-left {
                display: flex;
                align-items: center;

                p {
                    margin-left: 14px;
                    font-size: 15px;
                    font-weight: bold;
                    font-family: 'Gilroy bold', sans-serif;
                }
            }

            .header-center {
                ul {
                    li {
                        a {
                            font-weight: 700;
                            font-size: 15px;
                            margin: 0 15px;
                            color: black;
                        }
                    }
                }
            }

            .header-right {
                display: flex;
                align-items: center;

                .profile {
                    display: flex;
                    align-items: center;
                    text-decoration: none;

                    p {
                        font-size: 17px;
                        color: #787993;
                        margin-right: 12px;
                    }
                }

                ul {
                    &:first-of-type {
                        li {
                            a {
                                font-weight: 500;
                                font-size: 14px;
                                color: #4A4A4A;
                                padding: 5px 10px;
                                display: inline-block;
                            }

                            .active-language {
                                position: relative;
                                z-index: 2;

                                &::before {
                                    content: "";
                                    background: rgba(246, 218, 194, 0.831324);
                                    width: 100%;
                                    height: 4px;
                                    position: absolute;
                                    top: 0;
                                    bottom: 0;
                                    right: 0;
                                    left: 0;
                                    margin: auto;
                                    z-index: 1;
                                    transform: rotate(-45deg);
                                }
                            }

                            &:first-child {
                                a {
                                    &:first-child {
                                        border-right: solid 2px #4A4A4A;
                                    }
                                }
                            }
                        }
                    }

                    &:last-of-type {
                        margin-left: 60px;
                        align-items: center;

                        li {
                            a {
                                font-weight: bold;
                                font-size: 17px;
                                color: #4E72CA;
                                display: inline-block;
                                margin: 2px 10px;
                            }

                            .active {
                                border-bottom: #4e71cac4 solid 4px;
                            }

                            &:first-of-type {
                                a {
                                    padding: 0 10px;
                                    border-right: 4px solid #4E72CA;
                                }
                            }
                        }
                    }
                }
            }
        }

        .header-bottom {
            display: flex;
            justify-content: space-between;
            margin-top: 17px;

            .header-bottom-left {
                overflow: hidden;
                border: 1px solid rgba(151, 151, 151, 0.496575);
                border-radius: 22.5px;

                select {
                    border: none;
                    outline: none;
                    background: transparent;
                    padding: 13px 30px;
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                }

                input {
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                    width: 300px;
                    border: none;
                    outline: none;
                    background: transparent;
                    padding: 13px 25px;
                    border-right: solid 2px rgba(151, 151, 151, 0.496575);
                    border-left: solid 2px rgba(151, 151, 151, 0.496575);

                    &::placeholder {
                        color: black;
                    }
                }

                button {
                    margin-left: 20px;
                    padding: 14.5px 18px;
                    background: #19D476;
                    border-radius: 22px;
                    border: none;
                    font-weight: bold;
                    font-size: 14px;
                    outline: none;
                    color: white;
                    font-family: 'Gilroy bold', sans-serif;
                }
            }

            .header-bottom-right {
                .adding-product {
                    padding: 13px 30px 13px 56px;
                    background: #19D476;
                    border: none;
                    outline: none;
                    border-radius: 22px;
                    font-weight: bold;
                    font-family: 'Gilroy bold', sans-serif;
                    color: white;
                    font-size: 15px;
                    position: relative;

                    &::before {
                        content: "+";
                        position: absolute;
                        left: 0;
                        top: 0;
                        bottom: 0;
                        background: #11B463;
                        border-radius: 50%;
                        width: 44px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-size: 35px;
                        height: 44px;
                    }
                }
            }
        }
    }
}